.cross-bar-glitch-text {
  main {
    .cross-bar-glitch {
      position: relative;

      .bars {
        @bars-dalay: 0.3s, 0.2s, 0.5s, 0.3s, 0.4s;

        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        overflow: hidden;

        .bar {
          flex: 1;
          background-color: white;
          border-radius: .5rem;
          animation: 0.6s cubic-bezier(0.4, 0.2, 0.175, 1) forwards;

          &:nth-child(odd) {
            transform: translateX(101%);
            animation-name: slide-left;
          }
          &:nth-child(even) {
            transform: translateX(-101%);
            animation-name: slide-right;
          }

          .loop(@i) when(@i <= 5) {
            &:nth-child(@{i}) {
              animation-delay: extract(@bars-dalay, @i);
            }
            .loop(@i+1);
          }
          .loop(1);
        }
      }

      .glitch {
        font-size: .48rem;
        color: white;
        opacity: 0;
        animation: reveal .3s .6s forwards;

        span {
          &:not(:last-child) {
            --ratio: calc(100% / var(--slice-count));
            --top: calc(var(--ratio) * (var(--i) - 1));
            --bottom: calc(var(--ratio) * (var(--slice-count) - var(--i)));

            position: absolute;
            left: 0;
            clip-path: inset(var(--top) 0 var(--bottom) 0);
            animation-duration: .3s;
          }

          &:nth-child(odd) {
            animation-name: slide-from-left;
          }
          &:nth-child(even) {
            animation-name: slide-from-right;
          }

          &:last-child {
            opacity: 0;
            animation: reveal .9s steps(1) forwards;
          }
        }
      }

      @keyframes slide-from-left {
        from {
          transform: translateX(-20%);
        }
        to {
          transform: translateX(0);
        }
      }
      @keyframes slide-from-right {
        from {
          transform: translateX(20%);
        }
        to {
          transform: translateX(0);
        }
      }

      @keyframes reveal {
        to {
          opacity: 1;
        }
      }

      @keyframes slide-left {
        to {
          transform: translateX(-101%);
        }
      }
      @keyframes slide-right {
        to {
          transform: translateX(101%);
        }
      }
    }
  }
}