.crimson-crescent-loading {

  main {
    background-color: #000;

    .loading {
      position: relative;
      width: 1.5rem;
      height: 1.5rem;
      border-radius: 50%;
      box-shadow: inset 8px 8px crimson;
      background-color: #000;
      animation: spin 2s linear infinite;

      &::before,
      &::after {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        border-radius: inherit;
        box-shadow: inherit;
        background-color: inherit;
      }

      &::before {
        filter: blur(5px);
      }
    
      &::after {
        filter: blur(10px);
      }
    }

    @keyframes spin {
      to {
        transform: rotate(1turn);
      }
    }
  }
}