.milk-scratch {
  main {
    background-color: black;

    p {
      position: absolute;
      z-index: 9;
      font-family: Lato, sans-serif;
      font-size: .55rem;
      color: white;
      user-select: none;
      filter: blur(1px) contrast(30);
      pointer-events: none;
    }

    .card {
      @milk-size: .3rem;

      display: flex;
      flex-wrap: wrap;
      width: @milk-size * 10;
      height: @milk-size * 10;
      padding: .2rem;
      background-color: black;
      filter: blur(15px) contrast(30);

      .milk {
        position: relative;
        width: @milk-size;
        height: @milk-size;
        border-radius: 50%;

        &::after {
          content: '';
          position: absolute;
          width: 100%;
          height: 100%;
          border-radius: inherit;
          background-color: white;
          transition: 25s cubic-bezier(0.19, 1, 0.22, 1);
        }

        &:hover::after {
          transform: scale(0);
          transition-duration: .3s;
        }
      }
    }
  }
}