.rotate-circles {
  main {
    background-color: #b3e5fc;

    .circle {
      @circle-colors: 
        #50514F,
        #F25F5C,
        #FFE066,
        #247BA0,
        #70C1B3,
        #1D3557,
        #88ABC0,
        #FFCDB2
      ;

      position: absolute;
      border-radius: 50%;
      background: conic-gradient(var(--color1) var(--pos), var(--color2) 0);

      .loop(@i) when (@i <= 8) {
        @animation-name: ~'color2-@{i}';
        
        &:nth-child(@{i}) {
          --color1: extract(@circle-colors, @i);

          z-index: 8 - @i;
          width: .4rem * @i;
          height: .4rem * @i;

          animation: pos 0.6s linear,
          color1 1.2s step-end,
          @animation-name 1.2s step-end;
          animation-iteration-count: 2;
          animation-delay: 0.4s * @i;
        }

        @keyframes @animation-name {
          50% {
            --color2: extract(@circle-colors, @i);
          }
        }
        .loop(@i+1);
      }
      .loop(1);
      
      @keyframes pos {
        to {
          --pos: 100%;
        }
      }

      @keyframes color1 {
        50% {
          --color1: transparent;
        }
      }
    }
  }
}
