.menu-hover-fill-text {
  main {
    .menu {
      display: flex;
      flex-direction: column;
      list-style: none;

      &-item {
        padding: 6px 0;
        
        a {
          display: block;
          padding: 4px 0;
          text-decoration: none;
          text-transform: uppercase;
          font: 700 .3rem Raleway, sans-serif;
          -webkit-text-stroke: 1px #198CE6;
          color: transparent;
          background: linear-gradient(#198CE6 0 100%) no-repeat left / 0;
          background-clip: text;
          transition: .3s linear;

          &:hover {
            background-size: 100%;
          }
        }
      }
    }
  }
}