.name-card {
  main {
    background-color: #eceffc;

    .shadow {
      filter: drop-shadow(0 2px 6px rgba(50, 50, 0, 0.5));  
    }

    .card {
      clip-path: inset(0 0 66.7% 0);
      transform: translateY(30%);
      transition: .5s ease;

      .card-header {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 3rem;
        height: .8rem;
        font-size: .24rem;
        color: white;
        background-color: #2979ff;
        clip-path: inset(0 20% 0 20%);
        transition: .5s ease;
      }

      .card-body {
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        width: 3rem;
        height: 1.6rem;
        background-color: white;
        clip-path: inset(0 20% 0 20%);
        transition: .5s ease;

        .contact-item {
          display: flex;
          align-items: center;
          font-size: .18rem;
          color: #333;
          opacity: 0;
          transform: translateY(100%);
          transition: .5s .2s ease;

          .icon {
            width: .26rem;
            height: .26rem;
            margin: 0 .1rem 0 .2rem;

            path {
              fill: #2979ff;
            }
          }
        }
      }

      &:hover {
        clip-path: inset(0 0 0 0);
        transform: translateY(0);

        .card-header {
          clip-path: inset(0 0 0 0);
        }
        .card-body {
          clip-path: inset(0 0 0 0);

          .contact-item {
            transform: translateY(0);
            opacity: 1;
          }
        }
      }
    }
  }
}