.gauge {
  main {
    .gauge-list {
      flex: 1;
      display: flex;
      justify-content: space-evenly;

      .gauge-item {
        display: flex;
        flex-direction: column;
        align-items: center;
        
        .gauge-title {
          font-size: .14rem;
          line-height: .4rem;
          text-transform: uppercase;
        }

        .gauge-content {
          --percentage: 0%;
          position: relative;
          width: .8rem;
          height: .8rem;
          border-radius: 50%;

          &::before {
            content: '''';
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            margin: auto;
            width: 90%;
            height: 90%;
            border-radius: inherit;
            background-color: #333;
            transform: scale(0);
            transition: .4s .2s ease;
          }

          &::after {
            content: attr(data-percentage);
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            opacity: 0;
            transition: .3s ease;
          }

          &:hover {
            &::before {
              transform: scale(1);
              transition-delay: 0;
            }
            &::after {
              opacity: 1;
              transition-delay: .2s;
            }
          }
        }

        &:nth-child(1) .gauge-content {
          background: conic-gradient(#e74d3d var(--percentage), #222 calc(var(--percentage) + 0.4%));
        }
        &:nth-child(2) .gauge-content {
          background: conic-gradient(#3398da var(--percentage), #222 calc(var(--percentage) + 0.4%));
        }
        &:nth-child(3) .gauge-content {
          background: conic-gradient(#2ecd70 var(--percentage), #222 calc(var(--percentage) + 0.4%));
        }
      }
    }
  }
}