.bouncing-3d-loading {
  main {
    background-color: #2980b9;

    .loading {
      display: flex;
      font-family: "Baloo Bhaijaan", cursive;
      font-size: .6rem;
      color: #fff;
      text-transform: uppercase;

      span {
        transform: translateY(30%);
        text-shadow: 
          0 0px #bbb, 
          0 1px #bbb, 
          0 2px #bbb, 
          0 3px #bbb,
          0 4px #bbb, 
          0 5px #bbb,
          0 6px #bbb,
          0 8px 8px rgba(0, 0, 0, .4);
        animation: bouncing .3s ease alternate infinite;
      }
    }

    @keyframes bouncing {
      to {
        transform: translateY(0);
        text-shadow: 
        0 0px #bbb, 
        0 1px #bbb, 
        0 2px #bbb, 
        0 3px #bbb,
        0 4px #bbb, 
        0 5px #bbb,
        0 6px #bbb,
        0 30px 16px rgba(0, 0, 0, .2);
      }
    }
  }
}