.particle-button {
  main {
    background: #eceffc;

    .btn {
      @primary-color: white;
      @pink-color: #ff0080;

      width: 1.2rem;
      height: .55rem;
      outline: none;
      border: none;
      border-radius: 3px;
      font-size: .16rem;
      color: #333;
      background-color: @primary-color;
      cursor: pointer;
      user-select: none;
      -webkit-tap-highlight-color: transparent;

      &-pink {
        font-family: Lato, sans-serif;
        color: white;
        background-color: @pink-color;
        box-shadow: 0 0 8px @pink-color;
      }

      &-bubbles {
        --bubble-one: radial-gradient(circle, @pink-color 20%, transparent 20%);
        --bubble-two: radial-gradient(circle, transparent 20%, @pink-color 20%,transparent 30%);

        position: absolute;
        transition: transform .1s ease-in;

        .bubbles {
          position: absolute;
          top: -.3rem;
          right: -.3rem;
          bottom: -.3rem;
          left: -.3rem;
          background-repeat: no-repeat;
          animation: bubbles 1s ease-in-out forwards;
        }

        @keyframes bubbles {
          0% {
            background-position: 
              18% 40%, 20% 31%, 30% 30%, 40% 30%, 50% 30%, 57% 30%, 65% 30%, 80% 32%, 
              15% 60%, 83% 60%, 18% 70%, 25% 70%, 41% 70%, 50% 70%, 64% 70%, 80% 71%;
          }
          50% {
            background-position: 
              10% 44%, 0% 20%, 15% 5%, 30% 0%, 42% 0%, 62% -2%, 75% 0%, 95% -2%, 
              0% 80%, 95% 55%, 7% 99%, 24% 99%, 41% 99%, 55% 95%, 68% 96%, 95% 99%;
          }
          100% {
            background-position: 
              5% 44%, -5% 20%, 7% 5%, 23% 0%, 37% 0, 58% -2%, 80% 0%, 99% -2%, 
              -5% 80%, 99% 55%, 2% 99%, 23% 99%, 42% 99%, 60% 95%, 70% 96%, 99% 99%;
            background-size: 0 0;
          }
        }

        &:active {
          transform: scale(0.95);
          background-color: darken(@pink-color, 2%);

          .bubbles {
            animation: none;
            background-size: 0;
          }
        }
      }
    }
  }
}