.confirm-modal {
  main {
    background: #eceffc;

    .btn {
      @primary-color: white;
      @danger-color: #f14668;
      @confirm-color: #48c774;

      width: 1.2rem;
      height: .4rem;
      outline: none;
      border: none;
      border-radius: 3px;
      font-size: .16rem;
      color: #333;
      background-color: @primary-color;
      cursor: pointer;
      user-select: none;
      -webkit-tap-highlight-color: none;

      &:hover {
        background-color: darken(@primary-color, 3%);
      }

      &-danger {
        background-color: @danger-color;
        color: white;
        &:hover {
          background-color: darken(@danger-color, 3%);
        }
      }

      &-confirm,
      &-cancel {
        position: relative;
        overflow: hidden;
        border-radius: .6rem;
        font-size: .18rem;
        font-weight: bold;

        &:hover {
          color: white;
        }

        &::before {
          content: '';
          position: absolute;
          z-index: 0;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          border-radius: inherit;
          transition: .3s ease;
        }

        &::after {
          content: attr(data-text);
          position: relative;
          transition: .3s ease;
        }
      }

      &-confirm {
        color: @confirm-color;

        &::before {
          transform: translateX(100%);
          background: @confirm-color;
        }
        &:hover {
          &::before {
            transform: translateX(0);
          }
        }
      }

      &-cancel {
        color: @danger-color;

        &::before {
          transform: translateX(-100%);
          background: @danger-color;
        }
        &:hover {
          &::before {
            transform: translateX(0);
          }
        }
      }
    }

    .modal {
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      margin: auto;
      border: none;
      border-radius: .1rem;
      box-shadow: 
        0 2.8px 2.2px rgba(0, 0, 0, 0.02),
        0 6.7px 5.3px rgba(0, 0, 0, 0.028), 
        0 12.5px 10px rgba(0, 0, 0, 0.035),
        0 22.3px 17.9px rgba(0, 0, 0, 0.042), 
        0 41.8px 33.4px rgba(0, 0, 0, 0.05),
        0 100px 80px rgba(0, 0, 0, 0.07);
      background: linear-gradient(to right, #0acffe 0%, #495aff 100%);
      animation: show-modal .5s ease;

      &-content {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
        width: 3.3rem;
        height: 2.6rem;
        color: white;

        .modal-icon {
          animation: show-modal-icon .5s .2s ease forwards;
          opacity: 0;
        }
        .modal-title {
          animation: show-modal-text .5s .35s ease forwards;
          opacity: 0;
        }
        .modal-description {
          animation: show-modal-text 1s .5s ease forwards;
          opacity: 0;
        }
        .modal-options button {
          margin: 0 .1rem;
          animation: show-modal-option .5s .65s ease forwards;
          opacity: 0;
        }
      }
    }

    @keyframes show-modal {
      from {
        transform: scale(0.8);
      }
    
      50% {
        transform: scale(1.1);
      }
    
      to {
        transform: scale(1);
      }
    }
        
    @keyframes show-modal-icon {
      from {
        transform: scale(0.4);
        opacity: 0;
      }

      50% {
        transform: scale(1.2);
        opacity: 1;
      }

      to {
        transform: scale(1);
        opacity: 1;
      }
    }

    @keyframes show-modal-text {
      from {
        transform: scale(0.6);
        opacity: 0;
      }

      50% {
        transform: scale(1.2);
        opacity: 1;
      }

      to {
        transform: scale(1);
        opacity: 1;
      }
    }

    @keyframes show-modal-option {
      from {
        transform: scale(0.4);
        opacity: 0;
      }

      50% {
        transform: scale(1.2);
        opacity: 1;
      }

      to {
        transform: scale(1);
        opacity: 1;
      }
    }
  }
}